.container {
  background-image: url(./background-city-image.png);
  background-size: cover;
  height: 100%;
  }
  
  .icon-play {
  background-image: url(./hamburger.png);
  background-size: cover;
  display: inline-block;
  margin-top: 8px;
  margin-left: 0px;
  margin-right: 10px;
  height: 20px;
  width: 20px;
  }
  
  .dropdown-toggle::after {
  display: none;
  }
  
  .divider:after,
  .divider:before {
  content: "";
  flex: 1;
  height: 1px;
  background: black;
  }
  
  .h-custom {
  height: calc(100% - 73px);
  }
  
  .btns-center {
  text-align: center;
  }
  
  .btns-padd {
  padding: 0.5rem 3rem;
  }
  
  
  
  
  .App {
  text-align: center;
  }
  
  .App-logo {
  height: 40vmin;
  pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
  .App-logo {
  animation: App-logo-spin infinite 20s linear;
  }
  }
  
  .App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  }
  
  .App-link {
  color: #61dafb;
  }
  
  @keyframes App-logo-spin {
  from {
  transform: rotate(0deg);
  }
  to {
  transform: rotate(360deg);
  }
  }